.navigation {
  background-color: red;
  background-color: #81b3d2;
}
.navigation__link {
  font-size: 18px;
  line-height: 40px;
  font-weight: bold;
  color: #ffffff;
  text-decoration: none;
  display: flex;
  align-items: center;
}
@font-face {
  font-family: "PT Sans";
  src: url("/source/fonts/ptsans.woff2") format("woff2"), url("/source/fonts/ptsans.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "PT Sans";
  src: url("/source/fonts/ptsansbold.woff2") format("woff2"), url("/source/fonts/ptsansbold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
body {
  font-family: "PT Sans", "Arial", sans-serif;
  margin: 0;
  padding: 0;
  text-transform: uppercase;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.welcome {
  line-height: 26px;
}
.welcome__image {
  background-image: url("../img/bg-back-mobile@1x.jpg");
  width: auto;
  min-height: 400px;
  background-size: cover;
}
.welcome__description {
  font-size: 18px;
  font-weight: bold;
}
.welcome__description span {
  font-size: 14px;
}
.advantages__list {
  padding: 0;
  margin: 0;
}
.advantages__promo {
  background-color: #81b3d2;
  color: #ffffff;
  font-size: 14px;
  line-height: 21px;
}
.advantages__promo__title {
  font-weight: bold;
  font-size: 18px;
}
.advantages__list-item p:first-of-type {
  font-size: 18px;
  font-weight: bold;
}
.list-item__info {
  font-size: 14px;
  line-height: 21px;
}
.bottom-list {
  background-color: #eeeeee;
}
.information__title {
  font-size: 21px;
  line-height: 26px;
  font-weight: bold;
}
.information-details {
  font-size: 14px;
  line-height: 24px;
}
.main-style-btn {
  background-color: #81b3d2;
  color: #ffffff;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
  font-weight: bold;
  width: 100%;
  padding: 20px 20px;
  border-style: none;
}
.welcome-galary__img {
  min-height: 161px;
}
.welcome-galary__wrapper {
  display: none;
}
.galary__title {
  font-size: 18px;
  line-height: 26px;
}
.galary__promo {
  font-size: 14px;
  line-height: 24px;
}
